.tickets {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 75%;
  padding-left: 30px;

}

.tickets__list {
  display: flex;
  flex-direction: column;

  width: 100%;
  margin: 0;
  padding: 0;

  list-style: none;
}

.tickets__item {
  width: 100%;
  margin-bottom: 20px;

  &:last-child {
    margin-bottom: 15px;
  }
}

.tickets__not-found {
  text-align: center;
  margin-top: 50%;
  text-transform: uppercase;
}

.tickets__button {
  padding: 5px 60px;

  font-size: 17px;
  color: #000000c9;

  cursor: pointer;
}
